.warning-lead {
  background-color: rgb(212, 30, 30);
  height: auto;
  width: 100%;
  padding: 0.5rem;
  box-sizing: border-box;
}

.warning-lead span {
  color: white;
  font-size: 1rem;
}

.sticky {
  position: fixed;
  top: 0;
  z-index: 999; 
}

.uk-divider-vertical {
  height:auto;
}

.footer {
  color: rgb(105, 105, 105);
  background-color: #080808;
  margin-top: 1rem;
}

.footer-bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4rem;
  background-color: #181818;
}

.small-box {
  background-color: rgb(245, 245, 245);
  border-radius: 0.3em;
  padding: 2rem;
  margin: 0.5rem;
}

.uk-label-danger {
  background-color: #f0506e !important;
  color: #fff !important;
}